import {AfterViewInit, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {IFaqs} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';
import {FaqService} from '../../../services/faq.service';
import {Router} from '@angular/router';

@Component({
    selector: 'app-faq',
    templateUrl: './faq.component.html',
    styleUrls: ['./faq.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class FaqComponent implements OnInit, AfterViewInit {

    public faqs: IFaqs[] = [];
    loading = false;
    errorMessage = '';

    constructor(private faqService: FaqService, public router: Router) {
    }

    ngOnInit() {
        this.getFaq();
    }

    getFaq() {
        this.loading = true;
        this.faqService.getFaqsGuest().subscribe((faqs: IFaqs[]) => {
            this.faqs = faqs;
            this.loading = false;
        }, err => {
            this.loading = false;
            this.errorMessage = Constants.handleError(err, this.router);
        })
    }

    ngAfterViewInit() {
        document.getElementById('preloader').classList.add('hide');
    }

}
