import {ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation} from '@angular/core';
import {Feedbacks} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {FeedbacksService} from '../../../services/feedbacks.service';
import {Constants} from '../../../app.constants';
import {Location} from '@angular/common';

@Component({
    selector: 'app-feedback',
    templateUrl: './feedback.component.html',
    styleUrls: ['./feedback.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class FeedbackComponent implements OnInit {

    public feedbacks: Feedbacks[] = [];
    public feedback: Feedbacks;
    public selected = [];
    public NewFeedbackForm: FormGroup;
    errorMessage: any[];
    loading = false;
    onSuccess: any;

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private feedbacksService: FeedbacksService,
                private changeDetectorRef: ChangeDetectorRef,
                private elem: ElementRef,
                private _location: Location) {
    }

    ngOnInit() {
        this.buildForm();
    }


    backClicked() {
        this._location.back();
    }


    buildForm() {
        this.NewFeedbackForm = this.formBuilder.group({
            content: ['', Validators.compose([Validators.required])],
            first_name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            last_name: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            phone: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            email: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
        })
    }


    onSubmit({value, valid}: { value: Feedbacks, valid: boolean }) {
        this.loading = true;
        if (value) {
            this.feedbacksService.createFeedback(value)
                .subscribe((feedbacks: Feedbacks) => {
                        if (feedbacks) {
                            Constants.succeeded('Your Feedback Has been received! We will respond shortly', 'Successful')
                        }
                        this.loading = false;
                    }, (err) => {
                        Constants.handleError(err, this.router, true);
                        this.loading = false;
                    }
                )
        }

    }

}
